<script lang="ts">import { createEventDispatcher, getContext } from "svelte";
import { ContextMenu } from "@xbs/svelte-menu";
import HeaderCard from "./HeaderCard.svelte";
export let columns;
export let areasMeta;
export let api;
export let contentEl;
const _ = getContext("wx-i18n").getGroup("kanban");
const dispatch = createEventDispatcher();
const { readonly, columnShape } = api.getReactiveState();
const getMenuItems = (column, columnIndex, columns, columnShape) => {
    const menuItems = columnShape.menu.items({
        columns,
        columnIndex,
        column,
    });
    if (!menuItems || !menuItems.length) {
        return null;
    }
    return menuItems.map(item => {
        return Object.assign(Object.assign({}, item), { text: _(item.text), css: item.disabled ? "disabled" : "" });
    });
};
let columnMenuItems = {};
$: {
    columns.forEach((column, i) => {
        columnMenuItems[column.id] = getMenuItems(column, i, columns, $columnShape);
    });
}
function handleMove(column, dir) {
    var _a;
    const columnIndex = columns.findIndex(c => c.id === column);
    const beforeIndex = dir === "left" ? columnIndex - 1 : columnIndex + 2;
    const before = (_a = columns[beforeIndex]) === null || _a === void 0 ? void 0 : _a.id;
    dispatch("action", {
        action: "move-column",
        data: {
            id: column,
            before,
        },
    });
}
let renamingCard = null;
function handleMenuAction(ev) {
    const { action, item } = ev.detail;
    if (action) {
        if (action.onClick) {
            action.onClick({
                id: action.id,
                item: action,
                column: item,
            });
            return;
        }
        switch (action.id) {
            case "add-card":
                dispatch("action", {
                    action: "add-card",
                    data: {
                        columnId: item.id,
                        card: {
                            label: _("Untitled"),
                        },
                    },
                });
                break;
            case "set-edit":
                if ($readonly.edit)
                    renamingCard = item.id;
                break;
            case "delete-column":
                dispatch("action", {
                    action: "delete-column",
                    data: {
                        id: item.id,
                    },
                });
                break;
            case "move-column:left":
                handleMove(item.id, "left");
                break;
            case "move-column:right":
                handleMove(item.id, "right");
                break;
        }
    }
}
let showMenu = null;
// FIXME - temporary fix for the https://tracker.webix.io/issue/SVAR-1166
const showMenuFix = ev => {
    ev.cancelBubble = true;
    return showMenu(ev);
};
let menuItems = [];
function resolver(item) {
    menuItems = columnMenuItems[item] || [];
    return columns.find(c => c.id === item);
}
const handleColumnActions = ({ detail: { action, data } }) => {
    if (action === "close-column-input")
        renamingCard = null;
    dispatch("action", { action, data });
};
</script>

<div class="wx-header" on:click={showMenuFix}>
	{#each columns as column (column.id)}
		<HeaderCard
			{column}
			isMenuVisible={$columnShape.menu.show && columnMenuItems[column.id]?.length}
			{contentEl}
			{api}
			{areasMeta}
			renaming={renamingCard === column.id}
			on:action={handleColumnActions} />
	{/each}
</div>

<ContextMenu
	at={'left-bottom'}
	options={menuItems}
	bind:handler={showMenu}
	{resolver}
	dataKey="menuId"
	on:click={handleMenuAction} />

<style>
	/* [todo] add ability to disable list item to wx, remove this block  */
	:global([data-id="wx-list-disabled"]) {
		opacity: 0.5;
		pointer-events: none;
	}
	.wx-header {
		display: flex;
		flex-direction: row;
		position: relative;

		padding: 12px 0 var(--wx-padding) 0;

		height: var(--wx-kanban-header-height);
		min-height: var(--wx-kanban-header-height);
	}</style>
