<script lang="ts">import { getContext } from "svelte";
import { getSortConfig } from "@xbs/lib-kanban";
import { uid } from "@xbs/lib-core-dom";
import Icon from "../Icon.svelte";
import { DropDownMenu } from "@xbs/svelte-menu";
const _ = getContext("wx-i18n").getGroup("kanban");
export let api;
export let options = null;
$: {
    options = Array.isArray(options) ? options : getSortConfig();
    options = options.map(el => {
        let { id, text, label, dir } = el;
        return Object.assign(Object.assign({}, el), { id: id || uid(), text: _(text || label), icon: dir === "asc" ? "wxi-asc" : "wxi-desc" });
    });
}
let preserve = null;
let stateSort;
$: {
    if (!stateSort) {
        stateSort = api.getReactiveState()
            .sort;
    }
    if ($stateSort === null || $stateSort === void 0 ? void 0 : $stateSort.preserve) {
        preserve = options.find(el => el.by === $stateSort.by && el.dir === $stateSort.dir);
    }
