<script>
	import { clickOutside } from "@xbs/lib-core-dom";
	import { createEventDispatcher } from "svelte";

	import ContextMenu from "./ContextMenu.svelte";
	import { Portal, Button } from "@xbs/svelte-wx";
	import { prepareMenuData } from "../helpers";

	const dispatch = createEventDispatcher();

	export let options;
	$: prepareMenuData(options);

	let active;
	let menuOptions = [];
	let activate = null;

	function cancelHover() {
		active = null;
	}

	function setMenu(ev, item, trigger) {
		// if the item has a submenu, show it and enable hover mode
		if (item.data && item.data.length) {
			if (active && trigger){
				// second click on item with submenu disables hover mode
				active = null;
			} else {
				menuOptions = item.data;
				active = item.id;
				activate(ev, item);
			}
		} else {
			// hide the submenu
			activate(null);
			// if it was the click action, dispatch it and end hover mode
			if (trigger){
				dispatch("click", { action: item });
				active = null;
			} else {
				// do not remove active flag, to preserve the hover mode
				active = -1;
			}
		}
	}

	function onHover(ev, item) {
		if (active)
			setMenu(ev, item);
	}

</script>

<div class="menubar" use:clickOutside={cancelHover}>
	{#each options as item (item.id)}
		<div
			class="menubar-item {active === item.id ? 'active' : ''}"
			on:mouseenter={ev => onHover(ev, item)}>
			<Button click={ev => setMenu(ev, item, true)}>{item.text}</Button>
		</div>
	{/each}
</div>

<Portal let:mount>
	<ContextMenu
		{mount}
		on:click
		options={menuOptions}
		bind:handler={activate}
		/>
</Portal>

<style>
	.menubar {
		display: flex;
		position: relative;
		width: -moz-fit-content;
		width: fit-content;
	}

	.menubar-item {
		--wx-button-background: transparent;
		--wx-button-font-family: var(--wx-font-family);
		--wx-button-font-weigth: var(--wx-font-weight);
		--wx-button-font-size: var(--wx-font-size);
	}

	.active,
	.menubar-item:hover {
		background-color: var(--wx-background-alt);
		border-radius: var(--wx-button-border-radius);
	}</style>
