import VirtualContent from "./VirtualContent.svelte";
import EditorContainer from "./EditorContainer.svelte";
export let editorShape;
export let cardTemplate;
export let layout;
export let editor;
export let api;
let contentEl;
const _ = getContext("wx-i18n").getGroup("kanban");
let { columns: storeColumns, rowKey: storeRowKey, rows: storeRows, readonly: storeReadonly, dragItemId: movedCardId, areasMeta, cardsMap, cardShape: rCardShape, cards: rCards, } = api.getReactiveState();
$: ({ edit, select, dnd } = $storeReadonly);
const { showModal } = getContext("wx-helpers");
let showMenu = null;
let menuItems = [];
const cardMenuItems = {};
const isMenuVisible = {};
$: {
    $rCards.forEach(card => {
        var _a;
        cardMenuItems[card.id] = getMenuItems(card, $rCardShape);
        isMenuVisible[card.id] = !!($rCardShape.menu.show && ((_a = cardMenuItems[card.id]) === null || _a === void 0 ? void 0 : _a.length));
    });
}
function resolver(id) {
    var _a;
    const card = $rCards.find(c => c.id == id);
    if (card) {
        if (((_a = api.getState().selected) === null || _a === void 0 ? void 0 : _a.length) > 1) {
            api.exec("select-card", { id: parseInt(id) });
        }
        menuItems = cardMenuItems[card.id] || [];
    }
    return card;
}
const getMenuItems = (card, cardShape) => {
    const menuItems = cardShape.menu.items({
        card,
    });
    if (!menuItems || !menuItems.length) {
        return null;
    }
    return menuItems.map(item => {
        return Object.assign(Object.assign({}, item), { text: _(item.text) });
    });
};
function handleMenuAction(e) {
    const { action, item } = e.detail;
    if (action) {
        if (action.onClick) {
            action.onClick({
                id: action.id,
                item: action,
                card: item,
            });
            return;
        }
        switch (action.id) {
            case "delete-card":
                showModal({
                    message: _("Would you like to delete this card?"),
                })
                    .then(() => {
                    api.exec("delete-card", { id: item.id });
                })
                    .catch(() => {
                    /* handling cancel */
                });
                break;
            case "set-edit":
                api.exec("set-edit", { cardId: item.id });
                break;
            case "duplicate-card":
                api.exec("duplicate-card", {
                    id: item.id,
                    card: {
                        label: `${_("Duplicate of")} ${item.label}`,
                    },
                });
                break;
        }
    }
}
