									{row}
									areaMeta={$areasMeta[getAreaId(column.id, row.id)]}
									cards={$cardsMap[getAreaId(column.id, row.id)]}
									{cardTemplate}
									{isMenuVisible} />
							{/each}
						</Row>
					{/each}
				{/if}
			{/if}
		</div>
	</div>
	{#if edit && !$movedCardId && editor.show !== false}
		<EditorContainer {api} config={editor} shape={editorShape} />
	{/if}
</div>

