<script lang="ts">import { elementsIds, defaultEditorConfig, } from "@xbs/lib-kanban";
import Editor from "./Editor.svelte";
export let config = defaultEditorConfig;
export let api;
export let shape;
const { edit } = api.getReactiveState();
$: editCard = api.getCard($edit === null || $edit === void 0 ? void 0 : $edit.cardId);
$: {
    config = Object.assign(Object.assign({}, defaultEditorConfig), config);
}
</script>

<div
	class="wx-sidebar"
	class:wx-sidebar-open={!!editCard}
	data-kanban-id={elementsIds.editor}
	on:click|stopPropagation>
	<Editor {api} {config} {shape} />
</div>

