<script lang="ts">import Icon from "../Icon.svelte";
export let name = "";
export let icon;
export let label;
export let disabled = false;
export let click = null;
function handleClick() {
    if (click) {
        click(name);
    }
}
</script>

<div
	class="wx-control"
	class:wx-disabled={disabled}
	on:click={handleClick}
	title={label}>
	<Icon name={icon} />
</div>

