<script lang="ts">import { createEventDispatcher, getContext } from "svelte";
import Card from "./Card.svelte";
import CardContainer from "./CardContainer.svelte";
import Icon from "../lib/Icon.svelte";
import { getAreaId, includesId, isSameId } from "@xbs/lib-kanban";
import VirtualScrollColumn from "./columnLayouts/VirtualScrollColumn.svelte";
import ScrollColumn from "./columnLayouts/ScrollColumn.svelte";
export let column;
export let areaMeta;
export let row;
export let cards;
export let cardTemplate = null;
export let api;
export let isMenuVisible;
const _ = getContext("wx-i18n").getGroup("kanban");
const { selected: rSelected, cardShape: rCardShape, cardsMeta: rCardsMeta, scroll: scrollTo, readonly: rReadonly, columnShape, before: rOverCardId, overAreaId: rOverColId, dragItemId: rMovedCardId, dragItemsCoords: rDragItemsCoords, layout: rLayout, cardHeight: rCardHeight, cardsMap, } = api.getReactiveState();
$: add = $rReadonly.add;
$: movedCardCoords = $rDragItemsCoords && $rDragItemsCoords[$rMovedCardId];
$: selected = $rSelected;
$: cardShape = $rCardShape;
$: cardsMeta = $rCardsMeta;
$: overCardId = $rOverCardId;
$: overColId = $rOverColId;
$: movedCardId = $rMovedCardId;
$: layout = $rLayout;
$: cardHeight = $rCardHeight;
const dispatch = createEventDispatcher();
function handleAddCardClick(e) {
    e.stopPropagation();
    dispatch("action", {
        action: "add-card",
        data: {
            columnId: column.id,
            rowId: row.id,
            card: {
                label: _("Untitled"),
            },
        },
    });
}
$: areaId = getAreaId(column.id, row.id);
$: areaHeight = areaMeta === null || areaMeta === void 0 ? void 0 : areaMeta.height;
$: areaDragHeight = areaHeight ? `${areaHeight}px` : "auto";
const borders = 2;
$: movedCardHeight = ((movedCardCoords === null || movedCardCoords === void 0 ? void 0 : movedCardCoords.height) || 50) - borders;
function buildColumnCss(columnShape, column, isOverLimit, cards) {
    let columnClass = "wx-column";
    if (column.collapsed)
        columnClass += " wx-collapsed";
    if (isOverLimit)
        columnClass += " wx-over-limit";
    if (column.css)
        columnClass += " " + column.css;
    if (columnShape && columnShape.css)
        columnClass += " " + columnShape.css(column, cards);
    return columnClass;
}
$: columnClass = buildColumnCss($columnShape, column, areaMeta.isOverLimit, $cardsMap[column.id]);
</script>

<div
	class={columnClass}
	data-drop-area={areaId}
	style="min-height:{areaDragHeight};">
	{#if !column.collapsed}
		{#if layout === 'column:lazy'}
