				{api}
				{column}
				{cards}
				{overCardId}
				{overColId}
				{movedCardId}
				{selected}
				{cardShape}
				{cardTemplate}
				{cardsMeta}
				{movedCardHeight}
				{areaId}
				{scrollTo}
				{cardHeight}
				{isMenuVisible}
				on:action />
		{:else if layout === 'column:default'}
			<ScrollColumn
				{api}
				{cards}
				{overCardId}
				{overColId}
				{selected}
				{cardShape}
				{cardTemplate}
				{cardsMeta}
				{movedCardHeight}
				{areaId}
				{cardHeight}
				{isMenuVisible}
				on:action />
		{:else}
			{#if cards}
				{#each cards as card (card.id)}
					{#if isSameId(card.id, overCardId)}
						<div
							class="wx-drop-area"
							style="min-height:{movedCardHeight}px;" />
					{/if}
					<CardContainer
						{api}
						cardTemplate={cardTemplate || Card}
						cardFields={card}
						on:action
						dragging={cardsMeta[card.id]?.dragging}
						selected={includesId(selected, card.id)}
						meta={cardsMeta && cardsMeta[card.id]}
						{cardShape}
						{cardHeight}
						menu={isMenuVisible[card.id]} />
				{/each}
			{/if}
			{#if !overCardId && isSameId(overColId, areaId)}
				<div
					class="wx-drop-area"
					style="min-height:{movedCardHeight}px;" />
			{/if}
		{/if}

		<div class="wx-controls-wrapper">
			{#if add && !areaMeta.noFreeSpace}
				<div class="wx-add-card-btn" on:click={handleAddCardClick}>
					<Icon name="wxi-plus" />
					<span class="wx-add-card-tip">
						{_('Add new card')}...
