<script lang="ts">import { getContext } from "svelte";
import { Select } from "@xbs/svelte-wx";
import { form } from "@xbs/lib-svelte";
import Search from "../Search.svelte";
const _ = getContext("wx-i18n").getGroup("kanban");
export let api;
export let showOptions = true;
export let options = [
    { id: null, label: _("Everywhere") },
    { id: "label", label: _("Label") },
    { id: "description", label: _("Description") },
];
$: cardsMeta = api && api.getReactiveState().cardsMeta;
let searchResults = null;
$: {
    if ($cardsMeta) {
        searchResults = Object.keys($cardsMeta).reduce((total, key) => {
            if ($cardsMeta[key].found) {
                total.push(api === null || api === void 0 ? void 0 : api.getCard(key));
            }
            return total;
        }, []);
        if (!searchResults.length) {
            searchResults = null;
        }
    }
}
const search = form({ value: "", by: options[0].id }, ({ value, by }) => {
    const item = options.find(option => option.id === by);
    api.exec("set-search", {
        value,
        by,
        searchRule: item === null || item === void 0 ? void 0 : item.searchRule,
    });
});
let changeHandler;
$: {
    if (!changeHandler) {
        changeHandler = (config) => {
            if ((config === null || config === void 0 ? void 0 : config.value) !== $search.value ||
                (config === null || config === void 0 ? void 0 : config.by) !== ($search === null || $search === void 0 ? void 0 : $search.by)) {
                search.reset(config);
            }
        };
        api.on("set-search", changeHandler);
    }
}
function handleSearchAction({ detail }) {
    const { id, action } = detail;
    switch (action) {
        case "result-click":
            api.exec("select-card", { id });
            break;
        case "search-focus":
            if ($search.value) {
                api.exec("set-search", {
                    value: $search.value,
                    by: $search.by,
                });
            }
            break;
    }
}
</script>

<Search
	bind:value={$search.value}
	{searchResults}
	on:action={handleSearchAction}>
	{#if showOptions}
		<div class="wx-select">
			<div class="wx-title">{_('Search in')}:</div>
			<Select bind:value={$search.by} {options} />
		</div>
	{/if}
</Search>

