<script lang="ts">import { afterUpdate, createEventDispatcher, onDestroy } from "svelte";
import { portal } from "@xbs/lib-kanban";
import Icon from "./../lib/Icon.svelte";
export let api;
export let contentEl;
export let column;
export let areasMeta;
export let isMenuVisible;
export let renaming = false;
const dispatch = createEventDispatcher();
const { readonly, columnShape, cardsMap } = api.getReactiveState();
$: edit = $readonly.edit;
let columnLabel = null;
let columnEl;
function endRenaming() {
    if (renaming && (columnLabel === null || columnLabel === void 0 ? void 0 : columnLabel.trim())) {
        dispatch("action", {
            action: "update-column",
            data: {
                id: column.id,
                column: {
                    label: columnLabel,
                },
            },
        });
    }
    renaming = false;
    columnLabel = null;
    dispatch("action", { action: "close-column-input", data: {} });
}
function handleColumnRename() {
    if (!edit) {
        return;
    }
    renaming = true;
}
function handleColumnInput(e) {
    columnLabel = e.target.value;
}
function handleInputKeypress(e) {
    if (e.charCode === 13) {
        endRenaming();
    }
}
function focus(node) {
    node.focus();
}
function handleColumnCollapse() {
    dispatch("action", {
        action: "update-column",
        data: {
            id: column.id,
            column: {
                collapsed: !column.collapsed,
            },
        },
    });
}
let columnOffset = null;
function positionCollapsedCol() {
    columnOffset = columnEl === null || columnEl === void 0 ? void 0 : columnEl.offsetLeft;
}
afterUpdate(() => {
    positionCollapsedCol();
});
let isValid;
if (false) {
    isValid = () => {
        if (typeof window === "undefined")
            return true;
        const l = location.hostname;
        const t = [
            // dhtmlx.com
            "ZGh0bWx4LmNvbQ==",
            // dhtmlxcode.com
            "ZGh0bWx4Y29kZS5jb20=",
            // webixcode.com
            "d2ViaXhjb2RlLmNvbQ==",
            // webix.io
            "d2ViaXguaW8=",
            // repl.co
            "cmVwbC5jbw==",
        ];
        for (let i = 0; i < t.length; i++) {
            const x = window.atob(t[i]);
            if (x === l || l.endsWith("." + x))
                return true;
        }
        return false;
    };
}
function buildColumnCss(columnShape, column, isOverLimit, cards) {
    let columnClass = "wx-column";
    if (column.collapsed)
        columnClass += " wx-collapsed";
    if (isOverLimit)
        columnClass += " wx-over-limit";
    if (column.css)
        columnClass += " " + column.css;
    if (columnShape && columnShape.css)
        columnClass += " " + columnShape.css(column, cards);
    return columnClass;
}
$: columnClass = buildColumnCss($columnShape, column, areasMeta[column.id].isOverLimit, $cardsMap[column.id]);
const ro = new ResizeObserver(() => {
    positionCollapsedCol();
});
$: if (contentEl)
    ro.observe(contentEl);
onDestroy(() => {
    ro.disconnect();
});
</script>

<div bind:this={columnEl} class={columnClass} data-column-header={column.id}>
